.btn{
    background-image: linear-gradient(to right,  hsl(136, 65%, 51%), hsl(192, 70%, 51%));
    padding:15px 40px;
    color:white;
    border:none;
    outline: none;
    border-radius: 40px;
    -webkit-border-radius: 40px;
    -moz-border-radius: 40px;
    -ms-border-radius: 40px;
    -o-border-radius: 40px;
    font-weight: 400;
    text-transform: capitalize;
    font-size: 18px;

    cursor: pointer;
}

.btn:hover{
     background-image: linear-gradient(to right,  hsl(136, 57%, 58%), hsl(192, 66%, 57%));
}