.header{
    background-color: hsl(0, 0%, 98%);
    width:100%;
    text-align: center;
}

.headerImgContainer{
    width:100%;
    background-image: url('../images/bg-intro-mobile.svg');
    background-repeat: no-repeat;
   background-size: cover;
   background-position: left center;
  
}

.headerImgContainer img{
    margin-top:-5%;
    width:100%;
    max-width: 35rem;
}

.headerInfoContainer{
    width: 95%;
    max-width: 27rem;
    margin: 40px auto 0 auto;
    padding: 10px 10px 50px 10px;
 
}

.title{
    color: hsl(233, 26%, 24%);
    font-weight: 400;
 
}

.paragraph{
    color: hsl(233, 8%, 62%);
    margin: 20px auto; 
}

@media (min-width:990px) {
        .header{
            min-height:100vh;
            display: flex;
            justify-content: space-between;
            flex-direction: row-reverse;
            align-items:center;
            position: relative;
        }

        .headerImgContainer{
            position: absolute;
            width:100%;
            height:100%;
            background-image: url('../images/bg-intro-desktop.svg');
            background-repeat: no-repeat;
        background-size: cover;
        background-position:center;
        right:-35%;
        z-index:2;
        }

        .headerImgContainer img{
            width:100%;
            margin-top: -8%;
            margin-right:10%;
            max-width: 55rem;
        
        }

        .headerInfoContainer{
            position: absolute;
            background-color: transparent;
            width: 30%;
            left:5%;
            z-index:1;
            text-align: left;

        }
} 