.footer{
    background-color: hsl(233, 26%, 24%);
    width:100%;
    min-height:fit-content;
    display:grid;
    place-items: center;
    text-align: center;
    gap:20px;
    padding:50px 0;

}


@media (min-width:990px) {

    .footer{
        grid-template-columns: repeat(4,auto);
        text-align: left;
       align-items: flex-start;
    }

   
    
}

ul{
    list-style: none;
}

.socialContainer{
    margin-top:20px;
    list-style: none;
    display: flex;
    align-items: center;
    gap:20px;
}

.list{
    padding:10px 0;
}

.link{
    color: hsl(220, 16%, 96%);
    font-size:18px;
    text-decoration: none;
}

.link:hover{
    color: hsl(136, 65%, 51%);
}

.col4 p{
    color: hsl(233, 8%, 62%);
    padding-top:20px;
}