.listContainer{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap:20px;
    text-align: center;
    
}

.list{
    width:80%;
    max-width:20rem;
}

@media (min-width:990px) {
    .list{
        text-align: left;
    }
    
}

.title{
    color: hsl(233, 26%, 24%);
    margin-top:20px;
    font-weight: 400;
    
}

.info{
    color: hsl(233, 8%, 62%);
    margin: 20px auto; 
}