
.navContainer{
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 20px;
    list-style: none;
}

.navButtonContainer{
    background-color: white;
    list-style: none;
    display: flex;
    flex-direction: column;
    row-gap: 20px;
    position: absolute;
    width: 80%;
    top: 100%;
    left: 0;
    right: 0;
    margin: 20px auto;
    border-radius: 10px;
    align-items: center;
    padding:10px 0;
    box-shadow: 2px 4px 16px 0px hsl(233deg 26% 24%);
}


.navList{
    width:fit-content;
    padding:25px;
    position: relative;
}

.navList::after{
    content:"";
    position: absolute;
    bottom:0;
    left:0;
    width:100%;
    height:5px;
    background-image:linear-gradient(to right,  hsl(136, 65%, 51%), hsl(192, 70%, 51%));
    transition: transform .2s;
    transform: scaleX(0);
    -webkit-transform: scaleX(0);
    -moz-transform: scaleX(0);
    -ms-transform: scaleX(0);
    -o-transform: scaleX(0);
    -webkit-transition: transform .2s;
    -moz-transition: transform .2s;
    -ms-transition: transform .2s;
    -o-transition: transform .2s;
}

.navList:hover::after{
    transform: scaleX(1);
    -webkit-transform: scaleX(1);
    -moz-transform: scaleX(1);
    -ms-transform: scaleX(1);
    -o-transform: scaleX(1);

}

.navLink{
    color: hsl(233, 8%, 62%);
    text-decoration: none;
    font-size: 18px;
}



.navList:hover .navLink{  
    color: hsl(233, 26%, 24%);
}