.whySection{
    background-color: hsl(220, 16%, 96%);
    width: 100%;
    min-height:100vh;
    display: grid;
    place-items:center;
    align-content: center;
    gap:7rem;
    padding:80px 0;
    

}



.whySectionTitle{
    width: 95%;
    max-width: 30rem;
    text-align: center;
}

@media (min-width:1200px) {
    .whySection{
    justify-items: start;
    justify-content: center;
    }

    .whySectionTitle{
        text-align: left;
    }
}

.title{
    color: hsl(233, 26%, 24%);
    font-weight: 400;
    font-size:2.3rem;
 
 
}

.paragraph{
    color: hsl(233, 8%, 62%);
    margin: 20px auto; 
}