.cardsContainer{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap:20px;
    text-align: left;
}

.card{
        width:80%;
        max-width:20rem;
        background-color: white;
        border-radius:10px;
        -webkit-border-radius:10px;
        -moz-border-radius:10px;
        -ms-border-radius:10px;
        -o-border-radius:10px;
        transition: all .2s;
        -webkit-transition: all .2s;
        -moz-transition: all .2s;
        -ms-transition: all .2s;
        -o-transition: all .2s;
}

.card:hover{
    box-shadow: 0px 3px 14px 0px hsl(233deg 19% 59%);
}

figcaption{
    padding:20px;
}

.card img{
    width: 20rem;
    height: 14rem;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}

.name{
    color: hsl(233, 8%, 62%);
}

.title{
    color: hsl(233, 26%, 24%);
    margin:10px 0;
    font-weight: 400;

 
}


.card:hover .title{
    color:hsl(192, 70%, 51%);
    cursor: pointer;
}

.info{
    color: hsl(233, 8%, 62%);
}